const getListOfNotes = () => {
    return (['A', 'A# / Bb', 'B', 'C', 'C# / Db', 'D', 'D# / Eb', 'E', 'F', 'F# / Gb', 'G', 'G# / Ab'])
}

const getChordsInKey = (key = []) => {
    if (key.length === 0) { return ({}) }
    const createChord = (type = '', notes = []) => { return { 'type': type, 'notes': notes } }
    const one = createChord('major', [key[0], key[2], key[4]])
    const two = createChord('minor', [key[1], key[3], key[5]])
    const three = createChord('minor', [key[2], key[4], key[6]])
    const four = createChord('major', [key[3], key[5], key[0]])
    const five = createChord('major', [key[4], key[6], key[1]])
    const six = createChord('minor', [key[5], key[0], key[2]])
    const seven = createChord('diminished', [key[6], key[1], key[3]])
    return ({ 'one': one, 'two': two, 'three': three, 'four': four, 'five': five, 'six': six, 'seven': seven })
}

const updateNotationTwo = (notes = [], showSharps = true) => {//chromatic
    const correctNotation = []
    for (let i = 0; i < 12; i++) {
        if (notes[i].length > 1) {
            if (showSharps) { correctNotation.push(notes[i].split(' / ')[0]) }
            else { correctNotation.push(notes[i].split(' / ')[1]) }
        } else { correctNotation.push(notes[i]) }
    }
    return correctNotation
}

const updateNotation = (notes = [], showSharps = true) => { //specific key
    const correctNotation = []
    for (let i = 0; i < 7; i++) {
        if (notes[i].length > 1) {
            if (showSharps) { correctNotation.push(notes[i].split(' / ')[0]) }
            else { correctNotation.push(notes[i].split(' / ')[1]) }
        } else { correctNotation.push(notes[i]) }
    }
    return correctNotation
}

const getCircleOfFifths = () => {
    const notes = getListOfNotes();
    const circleOfFifths = []
    for (let index = 0; index < 12; index++) {
        circleOfFifths.push(notes[(index + 7) % 12])
    }
    return (circleOfFifths)
}

const getKeysByRoot = (root = '', showSharps = true) => {
    const notes = getListOfNotes()
    let rootIndex = notes.indexOf(root)
    if (rootIndex === -1) {
        for (let i = 0; i < notes.length; i++) {
            notes[i].split(' / ')
            if (root === notes[i].split(' / ')[0] || root === notes[i].split(' / ')[1]) {
                rootIndex = i
            }
        }

    } if (rootIndex === -1) return { 'major': [], 'minor': [] }
    const major = []
    const minor = []
    major.push(notes[rootIndex])
    major.push(notes[(rootIndex + 2) % 12])
    major.push(notes[(rootIndex + 4) % 12])
    major.push(notes[(rootIndex + 5) % 12])
    major.push(notes[(rootIndex + 7) % 12])
    major.push(notes[(rootIndex + 9) % 12])
    major.push(notes[(rootIndex + 11) % 12])
    minor.push(notes[rootIndex])
    minor.push(notes[(rootIndex + 2) % 12])
    minor.push(notes[(rootIndex + 3) % 12])
    minor.push(notes[(rootIndex + 5) % 12])
    minor.push(notes[(rootIndex + 7) % 12])
    minor.push(notes[(rootIndex + 8) % 12])
    minor.push(notes[(rootIndex + 10) % 12])

    return {
        'major': {
            'notes': updateNotation(major, showSharps),
            'chords': getChordsInKey(updateNotation(major, showSharps))
        },
        'minor': {
            'notes': updateNotation(minor, showSharps),
            'chords': getChordsInKey(updateNotation(minor, showSharps))
        }
    }

}
export { getListOfNotes, getCircleOfFifths, getKeysByRoot, updateNotation, updateNotationTwo }